<template>
  <div id="app">
    <div class="centered-container">
      <!-- 标题 -->
      <div class="title-section">
        <h1>Image Segmentation for Traffic Safety</h1>
      </div>
      <!-- 上部分：摄像头画面 -->
      <div class="camera-section">
        <el-button
          @click="toggleCapture">{{ isCapturing? 'Stop Capturing' : 'Start Capturing' }}</el-button>
        <div v-if="isCapturing">
          <el-row>
            <el-col :span="12">
              <img
                :src="getVideoUrlWithTimestamp(videoUrl1)"
                alt="Camera 1"
                class="fixed-size-image"
              >
            </el-col>
            <el-col :span="12">
              <img
                :src="getVideoUrlWithTimestamp(videoUrl2)"
                alt="Camera 2"
                class="fixed-size-image"
              >
            </el-col>
          </el-row>
        </div>
      </div>
      <!-- 下部分：图片上传 -->
      <div class="upload-section">
        <el-upload
          class="upload-demo"
          action="/upload"
          :on-success="handleUploadSuccess"
          :before-upload="beforeUpload"
          :show-file-list="false"
        >
          <el-button
            size="small"
            type="primary"
          >Click to Upload Image</el-button>
        </el-upload>
        <!-- 显示上传的图片 -->
        <img
          v-if="uploadedImageUrl"
          :src="getImageUrlWithTimestamp(uploadedImageUrl)"
          alt="Uploaded Image"
          class="fixed-size-image"
        >
        <!-- 显示服务器返回的识别图片 -->
        <img
          v-if="serverReturnedImageUrl"
          :src="getImageUrlWithTimestamp(serverReturnedImageUrl)"
          alt="Server Returned Image"
          class="fixed-size-image"
        >
        <!-- 显示过滤后的图片 -->
        <img
          v-if="filteredImageUrl"
          :src="getImageUrlWithTimestamp(filteredImageUrl)"
          alt="Filtered Image"
          class="fixed-size-image"
        >
        <!-- 显示 best_params 和 best_scores -->
        <div>
          <h2>Gazelle Optimization Algorithm</h2>
          <p>Optimal Parameters <span class="gray-note">(An appropriate
              threshold can help the model more accurately determine which
              objects within the detection boxes are valid, reducing false
              positives and false negatives, thus improving the accuracy of the
              entire detection and classification system):</span>
          </p>
          <p>{{ bestParams }}</p>
          <p>Optimal Scores <span class="gray-note">(The optimal score is a
              quantitative indicator that measures the performance of the model
              when using the optimal parameters. A higher score means that the
              model performs better under the current optimization goal):</span>
          </p>
          <p>{{ bestScores }}</p>
        </div>
        <div>
          <h2>Whale Optimization Algorithm</h2>
          <p>Optimal Parameters <span class="gray-note">(The optimal threshold
              determined by the Whale Optimization Algorithm, which can optimize
              the model's detection performance):</span>
          </p>
          <p>{{ woaBestParams }}</p>
          <p>Optimal Scores <span class="gray-note">(The optimal score obtained
              by the Whale Optimization Algorithm, reflecting the model's
              performance under the optimal parameters):</span>
          </p>
          <p>{{ woaBestScores }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';
import { ElMessage } from 'element-plus';

const isCapturing = ref(false);
const videoUrl1 = '/video_feed1';
const videoUrl2 = '/video_feed2';
const uploadedImageUrl = ref(null);
const serverReturnedImageUrl = ref(null);
const filteredImageUrl = ref(null);
const bestParams = ref(null);
const bestScores = ref(null);
const woaBestParams = ref(null);
const woaBestScores = ref(null);

const toggleCapture = () => {
  isCapturing.value = !isCapturing.value;
};

const handleUploadSuccess = (response) => {
  console.log(response);

  // 输出解析后的数据
  console.log("Best Params:", response.best_params);
  console.log("Best Scores:", response.best_scores);
  console.log("WOA Best Params:", response.woa_best_params);
  console.log("WOA Best Scores:", response.woa_best_scores);
  console.log("Filename:", response.filename);

  // 将数组转换为字符串并替换方括号
  let paramsStr = JSON.stringify(response.best_params).replace(/\[|\]/g, '');
  let scoresStr = JSON.stringify(response.best_scores).replace(/\[|\]/g, '');
  let woaParamsStr = JSON.stringify(response.woa_best_params).replace(/\[|\]/g, '');
  let woaScoresStr = JSON.stringify(response.woa_best_scores).replace(/\[|\]/g, '');

  // 去掉引号
  bestParams.value = paramsStr.replace(/"/g, '');
  bestScores.value = scoresStr.replace(/"/g, '');
  woaBestParams.value = woaParamsStr.replace(/"/g, '');
  woaBestScores.value = woaScoresStr.replace(/"/g, '');

  uploadedImageUrl.value = `/uploads/${response.filename}`;
  // 服务器返回的识别图片
  serverReturnedImageUrl.value = `/uploads/processed_${response.filename}`;
  // 过滤后的图片
  filteredImageUrl.value = `/uploads/filtered_${response.filename}`;
};

const beforeUpload = (file) => {
  const isJPG = file.type === 'image/jpeg';
  const isLt2M = file.size / 1024 / 1024 < 2;

  if (!isJPG) {
    ElMessage.error('Only JPG format images can be uploaded!');
  }
  if (!isLt2M) {
    ElMessage.error('The uploaded image size cannot exceed 2MB!');
  }
  return isJPG && isLt2M;
};

const getVideoUrlWithTimestamp = (url) => {
  return `${url}?t=${Date.now()}`;
};

const getImageUrlWithTimestamp = (url) => {
  return `${url}?t=${Date.now()}`;
};
</script>

